<template>
  <div>
    <h2>基础用法</h2>
    <ul>
      <li>指定node-key为id</li>
      <li>通过data设置展示的数据</li>
    </ul>
  <vue-run-sfc
    title="基础用法"
    :open="true"
    :code="code"
    height="300px"
  ></vue-run-sfc>
  </div>
</template>

<script>
export default {
  data () {
    return {
      code: `<template>
  <SearchTree
    node-key="id"
    :data="treeList"
  ></SearchTree>
</template>

<script>
export default {
  data() {
    return {
      treeList: [{
        "id": 1000,
        "name": "河南省",
        "children": [{
          "id": 1009,
          "name": "新乡市",
          "children": [{
            "id": 1119,
            "name": "新乡市宏力大道店"
          }, {
            "id": 1120,
            "name": "新乡市胜利北街店"
          }, {
            "id": 1121,
            "name": "新乡市首比街店"
          }]
        },
        {
          "id": 1016,
          "name": "巩义市",
          "children": [{
            "id": 1254,
            "name": "巩义市新兴路店"
          }]
        }]
      }],
    }
  },
};
<\/script>`
    }
  }
}
</script>
